body {
  font-family: sans-serif;
}

.container {
  max-width: 100%;
  margin: 0;
  padding: 10px;
}

.wiki-item {
  border: 1px solid #ccc;
  margin: 10px;
  padding: 10px;
  box-shadow: 5px 5px 10px #0003;
}

.wiki-content {
  flex-direction: column;
  padding: 5px;
  display: flex;
}

.wiki-text, .img-container {
  min-width: 100%;
  max-width: 100%;
}

@media (width >= 600px) {
  .container {
    max-width: 960px;
    margin: 0 auto;
  }

  .wiki-content {
    flex-direction: row-reverse;
    display: flex;
  }

  .wiki-text, .img-container {
    min-width: 50%;
    max-width: 50%;
  }
}

